<template>
    <v-list id="main-nav" class="mt-n3" color="#363636">
      <div class="nav-logo mt-4 ml-1">
        <a href="/">
          <img
            height="45"
            width="45"
            :src="fromAssetsFolder('roundabout_logo.png')"
          />
        </a>
      </div>
      <template v-for="item in mainNavItems">
        <router-link
          :to="item.url"
          @click.native="setActiveUrl(item.url)"
          :key="item.text"
          v-if="!item.children"
        >
          <v-list-item
            :key="item.text"
            link
            class="mt-3"
            :class="{ active: item.active }"
          >
            <v-list-item-action>
              <v-chip
                class="chat-message-notifications"
                v-if="item.icon === 'mdi-chat' && totalUnseenChatMessages > 0"
                small
                style="margin-left: 26px"
                color="red darken-4"
                text-color="white"
              >
                {{ totalUnseenChatMessages }}
              </v-chip>
              <v-icon :class="{ 'chat-purple': item.icon === 'mdi-chat' }">{{
                item.icon
              }}</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>
                <span> {{ item.text }} </span>
                <v-chip
                  v-if="item.icon === 'mdi-trending-up'"
                  small
                  style="margin-left: 60px"
                  color="red darken-4"
                  text-color="white"
                >
                  Beta
                </v-chip>
                <v-chip
                  v-if="item.icon === 'mdi-radar'"
                  small
                  style="margin-left: 26px"
                  color="red darken-4"
                  text-color="white"
                >
                  Beta
                </v-chip>
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </router-link>
        <v-list-group
          v-else
          :key="item.text"
          v-model="item.model"
          :append-icon="item.model ? item.icon : item['icon-alt']"
          prepend-icon="mdi-account-cog-outline"
          class="mt-3"
        >
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title>{{ item.text }}</v-list-item-title>
            </v-list-item-content>
          </template>
          <template v-for="(child, i) in item.children">
            <router-link
              @click.native="setActiveUrl()"
              :to="child.url"
              :key="child.text"
            >
              <v-list-item :key="i" link :class="{ active: child.active }">
                <v-list-item-action>
                  <v-icon>{{ child.icon }}</v-icon>
                </v-list-item-action>
                <v-list-item-content>
                  <v-list-item-title>{{ child.text }}</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </router-link>
          </template>
        </v-list-group>
      </template>
      <v-btn
        light
        style="
          position: absolute;
          bottom: 64px;
          margin-left: 64px;
          width: 124px;
        "
        v-on:click="logout()"
        >Logout</v-btn
      >
    </v-list>
</template>

<script>
import { mapActions, mapState } from "vuex";

import fromAssetsFolder from './../utils';

export default {
  name: "Navigation",
  components: {},
  data() {
    return {
      mainNavItems: [
        {
          icon: "mdi-account-group-outline",
          text: "Community",
          url: "/community",
          active: false,
        },
        {
          icon: "mdi-chat",
          text: "Chat",
          url: "/chat",
          active: false,
        },
        {
          icon: "mdi-trending-up",
          text: "Your Campaigns",
          url: "/adaccounts",
          active: false,
        },
        {
          icon: "mdi-radar",
          text: "Your Audience trends",
          url: "/audience/insight",
          active: false,
        },
        {
          icon: "mdi-movie-outline",
          text: "Video Library",
          url: "/videolibrary",
          active: false,
        },
        {
          icon: "mdi-chevron-up",
          "icon-alt": "mdi-chevron-down",
          text: "Account settings",
          active: false,
          model: false,
          children: [
            {
              text: "Settings",
              url: "/account",
              icon: "mdi-tune",
              active: false,
            },
            {
              text: "Notification",
              url: "/notification/devices",
              icon: "mdi-bell-outline",
              active: false,
            },
            {
              text: "Social Media",
              url: "/account/settings/socialmedia",
              icon: "mdi-share-variant-outline",
              active: false,
            },
          ],
        },
      ],
      itemCampaigns: false,
      itemsSettings: [
        { title: "Click Me" },
        { title: "Click Me" },
        { title: "Click Me" },
        { title: "Click Me 2" },
      ],
    };
  },
  computed: {
    ...mapState({
      totalUnseenChatMessages: "totalUnseenChatMessages",
    }),

    user: function () {
      return this.$store.state.user;
    },
  },
  methods: {
    logout() {
      this.$store.commit("CLEAR_ALL_DATA");
      this.$router.push("/");
      location.reload();
    },
    setActiveUrl(userClick) {
      if (userClick === "/community") {
        this.$intercom.shutdown();
      }

      this.mainNavItems.forEach((menu) => {
        if (menu.url === window.location.hash.substring(1)) {
          menu.active = true;
        } else {
          menu.active = false;

          if (
            window.location.hash.substring(1) === "/" &&
            menu.url === "/community"
          ) {
            menu.active = true;
          }

          if (typeof menu.url === "undefined") {
            menu.children.forEach((child) => {
              if (child.url === window.location.hash.substring(1)) {
                child.active = true;
              } else {
                child.active = false;
              }
            });
          }
        }
      });
    },
  },
  mounted() {
    this.setActiveUrl();
  },
};
</script>

<style lang="scss" scoped>
#main-nav{
  background: #636363;
}
#main-nav a {
  text-decoration: none;
}

// .active {
// }

#main-nav:hover .nav-logo a {
  margin-left: 115px;
}
.chat-message-notifications {
  font-size: 10px !important;
  position: absolute;
  top: 5px;
  left: 3px;
  z-index: 999;
  height: 18px !important;
  padding: 6px;
}
</style>
